@import './colors.css';
@import './fonts.css';

.App-footer {
    grid-row: 3;
    grid-column: 2/-2;
    align-self: center;
    justify-self: center;
    align-content: center;
    margin: 0 20px;
    padding-bottom: 8px;
}

@media screen and (min-width: 1200px) {
    .App-footer {
        grid-column: 3/-3;
    }
}

@media screen and (min-width: 1400px) {
    .App-footer {
        grid-column: 4/-4;
    }
}

.App-footer p {
    font-family: 'Baloo Da 2', cursive;
    font-weight: 300;
    font-size: 14px;
    line-height: 18px;
    letter-spacing: 16px;
    text-transform: uppercase;
    color: var(--colour-theme);
    opacity: 1;
}
