@import './colors.css';
@import './fonts.css';

.App-header {
    position: relative;
    grid-row: 1;
    grid-column: 2/-2;
    align-self: center;
    justify-self: start;
    display: flex;
    flex-wrap: nowrap;
    justify-content: space-between;
    width: calc(100% - 40px);
    height: 100%;
    margin: 0 20px;
    padding-top: 5px;
    border-bottom: 1px solid var(--colour-header-light);
}

.App-header:after {
    position: absolute;
    bottom: 0;
    left: 0;
    right: 0;
    content: "";
    height: 100px;
    transform: translateY(101px);
    z-index: 20;
    background-image: linear-gradient(var(--colour-background), var(--colour-background-zero));
}

@media screen and (min-width: 1200px) {
    .App-header {
        grid-column: 3/-3;
    }
}

@media screen and (min-width: 1400px) {
    .App-header {
        grid-column: 4/-4;
    }
}

h1 {
    font-family: 'Baloo Da 2', cursive;
    font-weight: 800;
    font-size: 60px;
    line-height: 80px;
    text-align: left;
    color: var(--colour-theme);
}

.header-user-container {
    display: flex;
    flex-wrap: nowrap;
    align-content: center;
    height: 80px;
}

.header-user-username {
    align-self: center;
    font-family: 'Baloo Da 2', cursive;
    font-weight: 600;
    font-size: 30px;
    margin-right: 10px;
    color: var(--colour-header-light)
}

.header-user-icon {
    cursor: pointer;
    display: flex;
    justify-content: center;
    align-content: center;
    height: 50px;
    width: 50px;
    border-radius: 50%;
    padding: 8px;
    justify-self: center;
    align-self: center;
    outline: none;
    border: none;
    color: var(--colour-maxwhite);
    background-color: var(--colour-theme);
    transform: scale(0.8);
    animation-name: pulse;
    animation-duration: 6s;
    animation-iteration-count: infinite;
    animation-timing-function: ease-in;
}

@keyframes pulse {
    0% {background-color: var(--colour-theme)}
    2% {background-color: var(--colour-background)}
    10% {background-color: var(--colour-theme)}
    100% {background-color: var(--colour-theme)}
}

.header-user-icon:hover {
    transform: scale(0.85);
    animation: none;
    background-color: var(--colour-theme-light);
}