@import url(https://fonts.googleapis.com/css2?family=Baloo+Da+2:wght@400;500;600;700;800&display=swap);
:root {
    --colour-background: #222;
    --colour-background-zero: rgba(32,32,32, 0.0);

    --colour-theme: #0073a8;
    --colour-theme-light: #00aeff;

    --colour-header-light: #aaa;
    
    --colour-input-text: #aaa;  
    --colour-input-background: #333;
    
    --colour-message-line: #333;
    --colour-message-time: #666;
    --colour-message-text: #ccc;
    
    --colour-changeuser-title: #aaa;
    
    --colour-maxwhite: #fff;
}

*,:after,:before {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  height: 100vh;
  display: grid;
  grid-template-rows: 80px 1fr 20px;
  grid-template-columns: 20px 1fr 20px;
  background-color: var(--colour-background);
}

@media screen and (max-width: 540px) and (orientation: portrait) {
  body {
    grid-template-columns: 20px 1fr 20px
  }
}

@media screen and (min-width: 540px) {
  body {
      grid-template-columns: 
      1fr 
      270px 270px
      1fr;
  }
}

@media screen and (min-width: 800px) {
  body {
      grid-template-columns: 
      1fr 
      130px
      270px 270px
      130px
      1fr;
  }
}

@media screen and (min-width: 1000px) {
  body {
      grid-template-columns: 
      1fr 
      100px 130px
      270px 270px
      130px 100px
      1fr;
  }
}

@media screen and (min-width: 1200px) {
  body {
      grid-template-columns: 
      1fr 
      100px 100px 130px
      270px 270px
      130px 100px 100px
      1fr;
  }
}

@media screen and (min-width: 1400px) {
  body {
      grid-template-columns: 
      1fr 
      100px 100px 100px 130px
      270px 270px
      130px 100px 100px 100px
      1fr;
  }
}



.App-header {
    position: relative;
    grid-row: 1;
    grid-column: 2/-2;
    align-self: center;
    justify-self: start;
    display: flex;
    flex-wrap: nowrap;
    justify-content: space-between;
    width: calc(100% - 40px);
    height: 100%;
    margin: 0 20px;
    padding-top: 5px;
    border-bottom: 1px solid var(--colour-header-light);
}

.App-header:after {
    position: absolute;
    bottom: 0;
    left: 0;
    right: 0;
    content: "";
    height: 100px;
    transform: translateY(101px);
    z-index: 20;
    background-image: linear-gradient(var(--colour-background), var(--colour-background-zero));
}

@media screen and (min-width: 1200px) {
    .App-header {
        grid-column: 3/-3;
    }
}

@media screen and (min-width: 1400px) {
    .App-header {
        grid-column: 4/-4;
    }
}

h1 {
    font-family: 'Baloo Da 2', cursive;
    font-weight: 800;
    font-size: 60px;
    line-height: 80px;
    text-align: left;
    color: var(--colour-theme);
}

.header-user-container {
    display: flex;
    flex-wrap: nowrap;
    align-content: center;
    height: 80px;
}

.header-user-username {
    align-self: center;
    font-family: 'Baloo Da 2', cursive;
    font-weight: 600;
    font-size: 30px;
    margin-right: 10px;
    color: var(--colour-header-light)
}

.header-user-icon {
    cursor: pointer;
    display: flex;
    justify-content: center;
    align-content: center;
    height: 50px;
    width: 50px;
    border-radius: 50%;
    padding: 8px;
    justify-self: center;
    align-self: center;
    outline: none;
    border: none;
    color: var(--colour-maxwhite);
    background-color: var(--colour-theme);
    transform: scale(0.8);
    -webkit-animation-name: pulse;
            animation-name: pulse;
    -webkit-animation-duration: 6s;
            animation-duration: 6s;
    -webkit-animation-iteration-count: infinite;
            animation-iteration-count: infinite;
    -webkit-animation-timing-function: ease-in;
            animation-timing-function: ease-in;
}

@-webkit-keyframes pulse {
    0% {background-color: var(--colour-theme)}
    2% {background-color: var(--colour-background)}
    10% {background-color: var(--colour-theme)}
    100% {background-color: var(--colour-theme)}
}

@keyframes pulse {
    0% {background-color: var(--colour-theme)}
    2% {background-color: var(--colour-background)}
    10% {background-color: var(--colour-theme)}
    100% {background-color: var(--colour-theme)}
}

.header-user-icon:hover {
    transform: scale(0.85);
    -webkit-animation: none;
            animation: none;
    background-color: var(--colour-theme-light);
}
.App-main {
    grid-row: 2;
    grid-column: 2/-2;
    margin: 0 20px;
}

@media screen and (min-width: 1200px) {
    .App-main {
        grid-column: 3/-3;
    }
}

@media screen and (min-width: 1400px) {
    .App-main {
        grid-column: 4/-4;
    }
}

.main-messagelist-container {
    width: 100%;
    overflow: auto;
    height: calc(100vh - 150px);
}

ul {
    list-style: none;
    padding-right: 12px;
    margin-bottom: 20px;  
}

.messagelist-item {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    padding: 12px 0 8px 0;
    border-top: 1px solid var(--colour-message-line);
    border-radius: 4px;
    font-family: 'Baloo Da 2', cursive;
    font-weight: 300;
    font-size: 16px;
}


.messagelist-user {
    flex: 0 0 auto;
    margin-right: 8px;
    font-family: 'Baloo Da 2', cursive;
    font-weight: 600;
    font-size: 21px;
    line-height: 21px;
    color: var(--colour-theme);
}

.messagelist-time {
    flex: 1 1 auto;
    align-self: center;
    margin-left: 8px;
    font-family: 'Baloo Da 2', cursive;
    font-weight: 300;
    font-size: 16px;
    line-height: 24px;
    text-align: right;
    color: var(--colour-message-time);
}

.messagelist-text {
    align-items: flex-end;
    margin-top: 3px;
    flex: 1 0 100%;
    font-family: 'Baloo Da 2', cursive;
    font-weight: 300;
    font-size: 25px;
    line-height: 30px;
    color:  var(--colour-message-text);
}

.main-messageinput-container {
    width: 100%;
    height: 50px;
    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
    grid-row: 2;
}

.main-messageinput-container input {
    outline: none;
    width: 100px;
    flex: 1 1 60px;
    height: 36px;   
    padding: 0 10px;
    border: none;
    border: 1px solid var(--colour-theme);
    border-top-left-radius: 10px;
    border-bottom-left-radius: 10px;
    font-family: 'Baloo Da 2', cursive;
    font-weight: 400;
    font-size: 26px;
    line-height: 36px;
    color: var(--colour-input-text);
    background-color: var(--colour-input-background);
}

.main-messageinput-container button {
    cursor: pointer;
    display: flex;
    justify-content: center;
    align-items: center;
    outline: none;
    width: 45px;
    height: 36px;
    border: none;
    border-top-right-radius: 10px;
    border-bottom-right-radius: 10px;
    font-family: 'Baloo Da 2', cursive;
    font-weight: 700;
    font-size: 21px;
    line-height: 36px;
    text-align: center;
    color: var(--colour-maxwhite);
    background-color: var(--colour-theme);
}

.main-messageinput-container button:hover {
    background-color: var(--colour-theme-light);
}

.App-footer {
    grid-row: 3;
    grid-column: 2/-2;
    align-self: center;
    justify-self: center;
    align-content: center;
    margin: 0 20px;
    padding-bottom: 8px;
}

@media screen and (min-width: 1200px) {
    .App-footer {
        grid-column: 3/-3;
    }
}

@media screen and (min-width: 1400px) {
    .App-footer {
        grid-column: 4/-4;
    }
}

.App-footer p {
    font-family: 'Baloo Da 2', cursive;
    font-weight: 300;
    font-size: 14px;
    line-height: 18px;
    letter-spacing: 16px;
    text-transform: uppercase;
    color: var(--colour-theme);
    opacity: 1;
}

.App-changeuser {
    position: relative;
    width: 100%;
    height: 100%;
    grid-row: 1/-1;
    grid-column: 2/-2;
    visibility: hidden;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-content: center;
    background-color: var(--colour-background);
    z-index: 10;
}

.open {
    visibility: visible;
}

.changeuser-button-close {
    position: absolute;
    top: 20px;
    right: 20px;
    height: 50px;
    width: 50px;
    border-radius: 50%;
    outline: none;
    border: none;
    color: var(--colour-maxwhite);
    background-color: var(--colour-theme);
    transform: scale(0.8);
}

.changeuser-button-close:hover {
    background-color: var(--colour-theme-light);
    
}

h2 {
    align-self: center;
    font-family: 'Baloo Da 2', cursive;
    font-weight: 600;
    font-size: 40px;
    margin-right: 10px;
    margin-bottom: 50px;
    color: var(--colour-changeuser-title);
}

.changeuser-user-icon {
    display: flex;
    justify-content: center;
    align-content: center;
    height: 100px;
    width: 100px;
    border-radius: 50%;
    padding: 16px;
    justify-self: center;
    align-self: center;
    outline: none;
    border: none;
    color: var(--colour-maxwhite);
    background-color: var(--colour-theme);
    transform: scale(0.8);
}

.changeuser-user-name {
    align-self: center;
    font-family: 'Baloo Da 2', cursive;
    font-weight: 600;
    font-size: 28px;
    margin-right: 10px;
    color: var(--colour-theme);
}

.changeuser-form {
    align-self: center;
    flex: 0 0 200px;
    margin-top: 50px;
    display: flex;
}

.changeuser-form input {
    outline: none;
    justify-items: center;
    flex: 1 1 200px;
    height: 36px;   
    padding: 0 10px;
    border: 1px solid var(--colour-theme);
    border-top-left-radius: 10px;
    border-bottom-left-radius: 10px;
    font-family: 'Baloo Da 2', cursive;
    font-weight: 400;
    font-size: 26px;
    line-height: 36px;
    color: var(--colour-input-text);
    background-color: var(--colour-input-background);
}

.changeuser-form button {
    display: flex;
    justify-content: center;
    align-items: center;
    outline: none;
    width: 125px;
    height: 36px;
    border: none;
    border-top-right-radius: 10px;
    border-bottom-right-radius: 10px;
    font-family: 'Baloo Da 2', cursive;
    font-weight: 700;
    font-size: 21px;
    line-height: 36px;
    text-align: center;
    color: var(--colour-maxwhite);
    background-color: var(--colour-theme);
}

.changeuser-form button:hover {
    background-color: var(--colour-theme-light);
}

