@import './colors.css';
@import './fonts.css';

.App-main {
    grid-row: 2;
    grid-column: 2/-2;
    margin: 0 20px;
}

@media screen and (min-width: 1200px) {
    .App-main {
        grid-column: 3/-3;
    }
}

@media screen and (min-width: 1400px) {
    .App-main {
        grid-column: 4/-4;
    }
}
