@import './colors.css';
@import './fonts.css';

.App-changeuser {
    position: relative;
    width: 100%;
    height: 100%;
    grid-row: 1/-1;
    grid-column: 2/-2;
    visibility: hidden;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-content: center;
    background-color: var(--colour-background);
    z-index: 10;
}

.open {
    visibility: visible;
}

.changeuser-button-close {
    position: absolute;
    top: 20px;
    right: 20px;
    height: 50px;
    width: 50px;
    border-radius: 50%;
    outline: none;
    border: none;
    color: var(--colour-maxwhite);
    background-color: var(--colour-theme);
    transform: scale(0.8);
}

.changeuser-button-close:hover {
    background-color: var(--colour-theme-light);
    
}

h2 {
    align-self: center;
    font-family: 'Baloo Da 2', cursive;
    font-weight: 600;
    font-size: 40px;
    margin-right: 10px;
    margin-bottom: 50px;
    color: var(--colour-changeuser-title);
}

.changeuser-user-icon {
    display: flex;
    justify-content: center;
    align-content: center;
    height: 100px;
    width: 100px;
    border-radius: 50%;
    padding: 16px;
    justify-self: center;
    align-self: center;
    outline: none;
    border: none;
    color: var(--colour-maxwhite);
    background-color: var(--colour-theme);
    transform: scale(0.8);
}

.changeuser-user-name {
    align-self: center;
    font-family: 'Baloo Da 2', cursive;
    font-weight: 600;
    font-size: 28px;
    margin-right: 10px;
    color: var(--colour-theme);
}

.changeuser-form {
    align-self: center;
    flex: 0 0 200px;
    margin-top: 50px;
    display: flex;
}

.changeuser-form input {
    outline: none;
    justify-items: center;
    flex: 1 1 200px;
    height: 36px;   
    padding: 0 10px;
    border: 1px solid var(--colour-theme);
    border-top-left-radius: 10px;
    border-bottom-left-radius: 10px;
    font-family: 'Baloo Da 2', cursive;
    font-weight: 400;
    font-size: 26px;
    line-height: 36px;
    color: var(--colour-input-text);
    background-color: var(--colour-input-background);
}

.changeuser-form button {
    display: flex;
    justify-content: center;
    align-items: center;
    outline: none;
    width: 125px;
    height: 36px;
    border: none;
    border-top-right-radius: 10px;
    border-bottom-right-radius: 10px;
    font-family: 'Baloo Da 2', cursive;
    font-weight: 700;
    font-size: 21px;
    line-height: 36px;
    text-align: center;
    color: var(--colour-maxwhite);
    background-color: var(--colour-theme);
}

.changeuser-form button:hover {
    background-color: var(--colour-theme-light);
}
