@import './colors.css';
@import './fonts.css';

.main-messageinput-container {
    width: 100%;
    height: 50px;
    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
    grid-row: 2;
}

.main-messageinput-container input {
    outline: none;
    width: 100px;
    flex: 1 1 60px;
    height: 36px;   
    padding: 0 10px;
    border: none;
    border: 1px solid var(--colour-theme);
    border-top-left-radius: 10px;
    border-bottom-left-radius: 10px;
    font-family: 'Baloo Da 2', cursive;
    font-weight: 400;
    font-size: 26px;
    line-height: 36px;
    color: var(--colour-input-text);
    background-color: var(--colour-input-background);
}

.main-messageinput-container button {
    cursor: pointer;
    display: flex;
    justify-content: center;
    align-items: center;
    outline: none;
    width: 45px;
    height: 36px;
    border: none;
    border-top-right-radius: 10px;
    border-bottom-right-radius: 10px;
    font-family: 'Baloo Da 2', cursive;
    font-weight: 700;
    font-size: 21px;
    line-height: 36px;
    text-align: center;
    color: var(--colour-maxwhite);
    background-color: var(--colour-theme);
}

.main-messageinput-container button:hover {
    background-color: var(--colour-theme-light);
}
