@import './colors.css';
@import './fonts.css';

.main-messagelist-container {
    width: 100%;
    overflow: auto;
    height: calc(100vh - 150px);
}

ul {
    list-style: none;
    padding-right: 12px;
    margin-bottom: 20px;  
}

.messagelist-item {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    padding: 12px 0 8px 0;
    border-top: 1px solid var(--colour-message-line);
    border-radius: 4px;
    font-family: 'Baloo Da 2', cursive;
    font-weight: 300;
    font-size: 16px;
}


.messagelist-user {
    flex: 0 0 auto;
    margin-right: 8px;
    font-family: 'Baloo Da 2', cursive;
    font-weight: 600;
    font-size: 21px;
    line-height: 21px;
    color: var(--colour-theme);
}

.messagelist-time {
    flex: 1 1 auto;
    align-self: center;
    margin-left: 8px;
    font-family: 'Baloo Da 2', cursive;
    font-weight: 300;
    font-size: 16px;
    line-height: 24px;
    text-align: right;
    color: var(--colour-message-time);
}

.messagelist-text {
    align-items: flex-end;
    margin-top: 3px;
    flex: 1 0 100%;
    font-family: 'Baloo Da 2', cursive;
    font-weight: 300;
    font-size: 25px;
    line-height: 30px;
    color:  var(--colour-message-text);
}
